.footer {
  // height: 200px;
  background-color: $bc-red;
  color: #fff;
  padding: 32px 0;

  .nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    font-size: 16px;
    line-height: 24px;
    border-bottom: 1px solid #fff;
    padding-bottom: 16px;
    .menu{
      width: 70%;
      display: flex;
      padding: 12px 0;
      margin-bottom: 24px;
      li{
        padding-right: 48px;
        a{
          padding: 12px 0;
          text-decoration: none;
          color: #fff;
          &:hover{
            border-bottom: 2px solid #FFF;
          }
        }
      }
    }
    .socialIcon{
      width: 30%;
      display: flex;
      justify-content: flex-end;
      li{
        margin-left: 32px;
        width: 48px;
        height: 48px;
      }
    }
    .contact{
      width: 100%;
      p{
        display: flex;
        align-items: center;
        img{
          margin-right: 20px;
          width: 48px;
          height: 48px;
        }
        a{
          color: #fff;
          text-decoration: none;
          font-size: 36px;
          line-height: 54px;
        }
      }
    }
  }
  .bottom{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 32px;
    .copyRight{
      font-size: 16px;
      line-height: 24px;
    }
    .policy{
      a{
        color: #fff;
        text-decoration: none;
        font-size: 14px;
        line-height: 21px;
        margin-left: 48px;
      }
    }
  }
}
@media(max-width:992px){
  .footer {
    padding: 24px 29px;
    .nav{
      .menu{
        margin-bottom: 22px;
        li{
          padding-right: 40px;
        }
      }
      .socialIcon{
        li{
          margin-left: 24px;
          width: 24px;
          height: 24px;
        }
      }
      .contact{
        p{
          padding-bottom: 13px;
          img{
            margin-right: 10px;
            width: 24px;
            height: 24px;
          }
          a{
            font-size: 20px;
            line-height: 30px;
          }
        }
      }
    }
    .bottom{
      align-items: flex-start;
      padding-top: 24px;
      .policy{
        padding-top: 12px;
        a{
          padding-left: 24px;
        }
      }
    }
  }
}
@media(max-width:767px){
  .footer{
    padding: 24px 16px;
    .nav{
      flex-direction: row-reverse;
      align-items: flex-start;
      padding-bottom: 20px;
      .menu{
        display: none;
      }
      .contact{
        width: 70%;
        p{
          padding-bottom: 6px;
          img{
            margin-right: 5px;
            width: 20px;
            height: 20px;
          }
          a{
            font-size: 16px;
            line-height: 30px;
          }
        }
      }
    }
    .bottom{
     flex-direction: column;
     padding-top: 16px;
     .copyRight{
        font-size: 14px;
        line-height: 21px;
      }
     .policy{
       padding: 0;
      a{
        padding: 0;
        margin-top: 8px;
        display: block;
        margin-left: 0px;
      }
    }
    }
  }
}