.header {
  background-color: #AA0601;
  padding: 30px 88px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
  line-height: 30px;
  .menu{
    display: flex;
    a{
      color: #FFFFFF;
      text-decoration: none;
      padding:20px 32px;
      &:hover{
        border-bottom: 2px solid #fff;
      }
    }
  }
}
@media(max-width:992px){
  .header{
    padding: 30px 39px;
  }
}
@media(max-width:767px){
  .header{
    padding: 0;
    flex-direction: column;
    .logo{
      text-align: center;
      padding: 10px;
    }
    .menu{
      flex-wrap: wrap;
      li{
        width: 50%;
        text-align: center;
        border: 1px solid #fff;
        padding: 8px 0;
        a{
          color: #FFFFFF;
          text-decoration: none;
          padding:8px 54px;
          &:hover{
            border-bottom:none;
            background-color: #fff;
            color: #AA0601;
          }
        }
      }
    }
  }
}
